// components/dynamic-zone/how-it-works/index.tsx
'use client';

import {
  type AnimationPlaybackControls,
  animate,
  motion,
  useMotionValue,
  useMotionValueEvent,
  useReducedMotion,
  useTransform,
} from 'framer-motion';
import {
  Calendar,
  type LucideIcon,
  MessageCircle,
  Paintbrush,
  Scissors,
} from 'lucide-react';
import React, { useEffect, useMemo, useRef, useState } from 'react';

import { Container } from '../../container';
import { Heading } from '../../elements/heading';
import { Subheading } from '../../elements/subheading';

// components/dynamic-zone/how-it-works/index.tsx

// components/dynamic-zone/how-it-works/index.tsx

type StepInput = {
  title?: string | null;
  description?: string | null;
  ctaText?: string | null;
  ctaHref?: string | null;
};

type Step = {
  title: string;
  description: string;
  ctaText?: string;
  ctaHref?: string;
  icon: LucideIcon;
};

const clamp = (n: number, min: number, max: number) =>
  Math.max(min, Math.min(max, n));
const pad2 = (n: number) => String(n).padStart(2, '0');

const normDeg = (d: number) => {
  const x = ((((d + 180) % 360) + 360) % 360) - 180;
  return x === -180 ? 180 : x;
};

const closestRotation = (current: number, target: number) => {
  // find target equivalent (target + 360k) closest to current
  const diff = normDeg(current - target);
  return current - diff;
};

function DiamondMark() {
  return (
    <span
      aria-hidden
      className="inline-block h-4 w-4 rotate-45 rounded-[5px] shadow-[0_18px_40px_rgba(0,0,0,0.55),inset_0_0_0_1px_rgba(255,255,255,0.10)]"
      style={{
        background:
          'linear-gradient(145deg, rgba(178,147,61,0.98), rgba(178,147,61,0.28))',
      }}
    />
  );
}

function TogglePill({
  value,
  onChange,
  reduce,
}: {
  value: boolean;
  onChange: (v: boolean) => void;
  reduce: boolean;
}) {
  return (
    <div className="mt-4 inline-flex rounded-full bg-white/10 p-1 shadow-[inset_0_0_0_1px_rgba(255,255,255,0.06)]">
      <div className="relative flex">
        <motion.span
          aria-hidden
          className="absolute bottom-0 top-0 rounded-full bg-white/15 shadow-[0_12px_30px_rgba(0,0,0,0.35),inset_0_0_0_1px_rgba(255,255,255,0.08)]"
          animate={{ left: value ? 0 : '50%', width: '50%' }}
          transition={
            reduce
              ? { duration: 0 }
              : { type: 'spring', stiffness: 420, damping: 34 }
          }
        />
        <button
          type="button"
          onClick={() => onChange(true)}
          className={[
            'relative z-10 rounded-full px-6 py-2 text-sm',
            'text-white/85 transition',
            'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-white/25',
            value ? 'text-white' : 'hover:text-white',
          ].join(' ')}
        >
          Igen
        </button>
        <button
          type="button"
          onClick={() => onChange(false)}
          className={[
            'relative z-10 rounded-full px-6 py-2 text-sm',
            'text-white/85 transition',
            'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-white/25',
            !value ? 'text-white' : 'hover:text-white',
          ].join(' ')}
        >
          Nem
        </button>
      </div>
    </div>
  );
}

function Tick({
  a,
  radius,
  len,
  opacity,
}: {
  a: number; // deg
  radius: number; // px
  len: number; // px
  opacity: number;
}) {
  // NOTE:
  // - The ring (parent) rotates by "--dial" and by framer rotate.
  // - This tick container is placed on the circle via rotate(a) translateY(-radius).
  // - To keep the tick VERTICAL on screen, we counter-rotate the line by -(dial + a).
  return (
    <div
      aria-hidden
      className="absolute left-1/2 top-1/2"
      style={{
        transform: `translate(-50%, -50%) rotate(${a}deg) translateY(-${radius}px)`,
      }}
    >
      <span
        className="absolute left-0 top-0 w-px bg-white"
        style={{
          height: len,
          opacity,
          transformOrigin: 'top center',
          transform: `translateX(-0.5px) rotate(calc(0deg - var(--dial) - ${a}deg))`,
        }}
      />
    </div>
  );
}

function StepMarker({
  angleDeg,
  radius,
  stemLen,
  step,
  index,
  rot,
  reduce,
  isMobile,
  hidden,
  onPick,
}: {
  angleDeg: number;
  radius: number;
  stemLen: number;
  step: Step;
  index: number;
  rot: ReturnType<typeof useMotionValue<number>>;
  reduce: boolean;
  isMobile: boolean;
  hidden: boolean;
  onPick: () => void;
}) {
  const Icon = step.icon;

  // Keep stems/icons almost vertical (like reference), but leave a little residual tilt for “rotation feel”.
  // total rotation at this node is (rot + angleDeg). We cancel ~88% of it -> small lean remains.
  const counter = useTransform(rot, (r) => -(r + angleDeg) * 0.88);

  // Distance from center (0°) -> fade + slight scale back
  const rel = useTransform(rot, (r) => normDeg(r + angleDeg));
  const depthScale = useTransform(rel, (v) => {
    const a = Math.min(1, Math.abs(v) / 120);
    return 1 - a * 0.1;
  });
  const depthOpacity = useTransform(rel, (v) => {
    const a = Math.min(1, Math.abs(v) / 120);
    return 1 - a * 0.38;
  });

  const iconSize = isMobile ? 26 : 30;

  return (
    <div
      className="absolute left-1/2 top-1/2"
      style={{
        transform: `translate(-50%, -50%) rotate(${angleDeg}deg) translateY(-${radius}px)`,
      }}
    >
      <motion.div
        style={{
          rotate: counter,
          scale: depthScale,
          opacity: hidden ? 0 : depthOpacity,
          transformOrigin: '50% 0%',
        }}
        className="relative"
      >
        {/* stem */}
        <div
          aria-hidden
          className="absolute left-1/2 top-0 w-[2px]"
          style={{
            height: stemLen,
            transform: 'translateX(-1px)',
            background:
              'linear-gradient(to bottom, rgba(255,255,255,0), rgba(255,255,255,0.26), rgba(255,255,255,0.10))',
            filter: 'drop-shadow(0 14px 22px rgba(0,0,0,0.28))',
          }}
        />

        {/* icon at end */}
        <motion.button
          type="button"
          onClick={onPick}
          className={[
            'absolute left-1/2 grid place-items-center',
            'rounded-full',
            'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-white/25',
          ].join(' ')}
          style={{
            top: stemLen,
            transform: 'translate(-50%, -50%)',
            padding: isMobile ? 10 : 12, // big click target, but visually “clean”
            background: 'transparent',
          }}
          whileHover={reduce ? undefined : { scale: 1.04, y: -1 }}
          whileTap={reduce ? undefined : { scale: 0.98, y: 0 }}
          transition={
            reduce ? undefined : { type: 'spring', stiffness: 360, damping: 26 }
          }
          aria-label={`${pad2(index + 1)} – ${step.title}`}
        >
          <Icon
            aria-hidden
            className="text-white"
            style={{
              width: iconSize,
              height: iconSize,
              opacity: 0.62,
            }}
          />
        </motion.button>
      </motion.div>
    </div>
  );
}

export const HowItWorks = ({
  heading,
  sub_heading,
  steps,
}: {
  heading?: string | null;
  sub_heading?: string | null;
  steps?: StepInput[] | null;
}) => {
  const reduce = useReducedMotion();

  const fallback: Omit<Step, 'icon'>[] = useMemo(
    () => [
      {
        title: 'Időpont foglalás',
        description:
          'Kezdje meg útját a megújulás felé. Válassza ki az Önnek legmegfelelőbb időpontot kényelmes online rendszerünkön keresztül, vagy hívjon minket bizalommal egyedi igényeivel.',
        ctaText: 'Foglalás most',
        ctaHref: '#',
      },
      {
        title: 'Konzultáció',
        description:
          'Személyes találkozó szakértőinkkel, ahol részletesen felmérjük igényeit, hajtípusát és stílusát. Egy közös beszélgetés során megalkotjuk az Önre szabott, egyedi tervet.',
      },
      {
        title: 'Szolgáltatás',
        description:
          'Dőljön hátra és élvezze a prémium kezelést nyugodt, luxus környezetben. A legkiválóbb anyagok és legmodernebb technikák felhasználásával varázsoljuk újjá megjelenését.',
      },
      {
        title: 'Utóápolás',
        description:
          'A törődés nem ér véget a szalonban. Személyre szabott tanácsokkal és professzionális termék-ajánlással segítünk, hogy az eredmény tartósan ragyogó maradjon.',
        ctaText: 'Kapcsolat',
        ctaHref: '#',
      },
    ],
    []
  );

  // Fodrászat-releváns ikonok (4 step)
  const iconByIndex: LucideIcon[] = useMemo(
    () => [Calendar, MessageCircle, Scissors, Paintbrush],
    []
  );

  const resolvedSteps: Step[] = useMemo(() => {
    const src = Array.isArray(steps) && steps.length ? steps : fallback;
    return src.map((s, i) => ({
      title: (s.title ?? fallback[i]?.title ?? `Lépés ${i + 1}`) as string,
      description: (s.description ?? fallback[i]?.description ?? '') as string,
      ctaText: (s as any).ctaText ?? fallback[i]?.ctaText ?? undefined,
      ctaHref: (s as any).ctaHref ?? fallback[i]?.ctaHref ?? undefined,
      icon: iconByIndex[i] ?? Scissors,
    }));
  }, [steps, fallback, iconByIndex]);

  const len = resolvedSteps.length;

  const [active, setActive] = useState(0);
  const [auto, setAuto] = useState<boolean>(() => !reduce);
  const [dragging, setDragging] = useState(false);

  useEffect(() => {
    if (reduce) setAuto(false);
  }, [reduce]);

  const safeActive = clamp(active, 0, Math.max(0, len - 1));

  // ---- panel width (for responsive geometry) ----
  const panelRef = useRef<HTMLDivElement>(null);
  const [vw, setVw] = useState(0);

  useEffect(() => {
    const el = panelRef.current;
    if (!el) return;
    const ro = new ResizeObserver((entries) => {
      const w = entries?.[0]?.contentRect?.width ?? 0;
      setVw(w);
    });
    ro.observe(el);
    return () => ro.disconnect();
  }, []);

  const geom = useMemo(() => {
    const viewportW = Math.max(340, vw || 0);
    const isMobile = viewportW < 560;

    // (panel background etc. stays untouched) — only dial geometry here
    const arcH = isMobile ? 420 : 470;

    // Circle size/radius (bigger => flatter arc)
    const radius = clamp(
      viewportW * (isMobile ? 0.86 : 0.78),
      isMobile ? 460 : 560,
      isMobile ? 640 : 820
    );
    const size = radius * 2;

    // Circle top inside the stage (controls arc vertical placement)
    const peakY = isMobile ? 132 : 148;

    // Step distribution across the arc (wide enough so neighbors are visible near the ends)
    const stepSpan = isMobile ? 210 : 230;
    const stepStart = -stepSpan / 2;
    const stepAngles =
      len <= 1
        ? [0]
        : Array.from(
            { length: len },
            (_, i) => stepStart + (i * stepSpan) / (len - 1)
          );

    // Tick distribution (wider than steps, many ticks)
    // Tick distribution (FULL circle -> a panel csak a felső ívet “ablakolja ki”)
    const tickSpan = 360;
    const tickStart = -180;
    const tickCount = isMobile ? 132 : 176;

    return {
      viewportW,
      isMobile,
      arcH,
      radius,
      size,
      peakY,
      stepAngles,
      tickStart,
      tickSpan,
      tickCount,
    };
  }, [vw, len]);

  // ---- rotation (dial) ----
  const rot = useMotionValue(0); // degrees

  const animRef = useRef<AnimationPlaybackControls | null>(null);
  const stopAnim = () => {
    animRef.current?.stop();
    animRef.current = null;
  };

  const pauseUntilRef = useRef(0);
  const isDraggingRef = useRef(false);

  const pauseAuto = (ms = 11000) => {
    pauseUntilRef.current = Date.now() + ms;
  };

  // Center target: chosen step should land at 0°
  const rotTargetForIndex = (i: number) => {
    const ang = geom.stepAngles[i] ?? 0;
    return 0 - ang;
  };

  const snapTo = (i: number, soft = false) => {
    const idx = clamp(i, 0, Math.max(0, len - 1));
    const target = rotTargetForIndex(idx);

    stopAnim();

    if (reduce) {
      rot.set(target);
      return;
    }

    const current = rot.get();
    const closest = closestRotation(current, target);

    animRef.current = animate(
      rot,
      closest,
      soft
        ? { duration: 0.28, ease: [0.22, 1, 0.36, 1] }
        : { type: 'spring', stiffness: 260, damping: 32 }
    );
  };

  useEffect(() => {
    snapTo(safeActive, true);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [geom.viewportW]);

  useEffect(() => {
    snapTo(safeActive);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [safeActive]);

  // live highlight: whichever is closest to center (0°)
  const [liveActive, setLiveActive] = useState(safeActive);
  const lastLiveRef = useRef(0);

  const nearestIndexByRot = (rotDeg: number) => {
    let best = 0;
    let bestD = Infinity;
    for (let i = 0; i < geom.stepAngles.length; i++) {
      const target = rotTargetForIndex(i);
      const d = Math.abs(normDeg(rotDeg - target));
      if (d < bestD) {
        bestD = d;
        best = i;
      }
    }
    return best;
  };

  useMotionValueEvent(rot, 'change', (latest) => {
    const now = performance.now();
    if (now - lastLiveRef.current < 55) return;
    lastLiveRef.current = now;
    setLiveActive(nearestIndexByRot(latest));
  });

  const displayIndex = dragging ? liveActive : safeActive;
  const displayStep =
    resolvedSteps[clamp(displayIndex, 0, Math.max(0, len - 1))];

  // auto-loop
  useEffect(() => {
    if (reduce) return;
    if (!auto) return;
    if (len <= 1) return;

    const id = window.setInterval(() => {
      if (isDraggingRef.current) return;
      if (Date.now() < pauseUntilRef.current) return;
      setActive((a) => (a + 1) % len);
    }, 3400);

    return () => window.clearInterval(id);
  }, [auto, reduce, len]);

  // drag → rotation
  const dragStartRotRef = useRef(0);
  const DEG_PER_PX = geom.isMobile ? 0.17 : 0.14;

  const onDragStart = () => {
    pauseAuto(11000);
    isDraggingRef.current = true;
    setDragging(true);
    stopAnim();
    dragStartRotRef.current = rot.get();
  };

  const onDragEnd = () => {
    isDraggingRef.current = false;
    setDragging(false);
    const idx = nearestIndexByRot(rot.get());
    setActive(idx);
  };

  // keyboard
  const onKeyDown: React.KeyboardEventHandler<HTMLDivElement> = (e) => {
    if (e.key === 'ArrowLeft') {
      e.preventDefault();
      pauseAuto(11000);
      setActive((v) => clamp(v - 1, 0, len - 1));
    }
    if (e.key === 'ArrowRight') {
      e.preventDefault();
      pauseAuto(11000);
      setActive((v) => clamp(v + 1, 0, len - 1));
    }
  };

  const ticks = useMemo(
    () => Array.from({ length: geom.tickCount }, (_, i) => i),
    [geom.tickCount]
  );

  // CSS var for “dial rotation” (used to keep ticks vertical without per-tick motion hooks)
  const dialCss = useTransform(rot, (r) => `${r}deg`);

  // Center marker geometry (only ONE gold line + ONE diamond)
  const marker = useMemo(() => {
    const peak = geom.peakY;
    const iconY = geom.isMobile ? peak + 156 : peak + 176; // where the center icon sits
    return {
      labelY: peak - (geom.isMobile ? 46 : 50),
      diamondY: peak - (geom.isMobile ? 20 : 22),
      lineTop: peak - 2,
      lineBottom: iconY - 8,
      iconY,
      titleY: iconY + (geom.isMobile ? 30 : 32),
    };
  }, [geom.isMobile, geom.peakY]);

  return (
    <section className="relative">
      <Container className="mx-auto max-w-7xl py-20 md:py-28">
        {(heading || sub_heading) && (
          <header className="mx-auto mb-10 max-w-4xl text-center">
            {heading ? (
              <Heading className="text-center">{heading}</Heading>
            ) : null}
            {sub_heading ? (
              <Subheading className="mx-auto mt-4 max-w-2xl text-center">
                {sub_heading}
              </Subheading>
            ) : null}
          </header>
        )}

        <div
          ref={panelRef}
          tabIndex={0}
          onKeyDown={onKeyDown}
          className={[
            'relative overflow-hidden rounded-[28px] md:rounded-[32px]',
            'border border-white/10',
            'bg-[#0b0c0e]',
            'outline-none',
            'shadow-[0_40px_140px_rgba(0,0,0,0.55)]',
            'min-h-[610px] md:min-h-[710px]',
          ].join(' ')}
        >
          {/* vignette + lights */}
          <div
            aria-hidden
            className="pointer-events-none absolute inset-0"
            style={{
              backgroundImage:
                'radial-gradient(900px circle at 20% 12%, rgba(255,255,255,0.10), transparent 58%), radial-gradient(820px circle at 78% 18%, rgba(255,255,255,0.08), transparent 62%), radial-gradient(1200px circle at 50% 120%, rgba(0,0,0,0.88), transparent 58%), radial-gradient(1200px circle at 50% 50%, rgba(0,0,0,0.55), transparent 70%)',
            }}
          />

          {/* subtle grain */}
          <div
            aria-hidden
            className="pointer-events-none absolute inset-0 opacity-[0.10] mix-blend-overlay"
          >
            <svg
              className="h-full w-full"
              preserveAspectRatio="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <filter id="noiseFilter">
                <feTurbulence
                  type="fractalNoise"
                  baseFrequency="0.9"
                  numOctaves="3"
                  stitchTiles="stitch"
                />
              </filter>
              <rect
                width="100%"
                height="100%"
                filter="url(#noiseFilter)"
                opacity="0.6"
              />
            </svg>
          </div>

          {/* content */}
          <div className="relative z-10 flex h-full flex-col items-center px-6 py-10 md:px-10 md:py-12">
            {/* top center: auto-loop */}
            <div className="mt-2 text-center">
              <div className="text-sm font-medium text-white/85">
                Automatikus váltás
              </div>

              <TogglePill
                reduce={!!reduce}
                value={auto}
                onChange={(v) => {
                  pauseAuto(2500);
                  setAuto(v);
                }}
              />

              <div className="mt-6 max-w-xl text-center text-sm leading-relaxed text-white/55">
                Kattints az ikonokra, vagy húzd az ívet – billentyűzeten a
                nyilakkal is navigálhatsz.
              </div>
            </div>

            {/* dial + content */}
            <div className="relative mt-10 w-full flex-1">
              {/* arc stage */}
              <div
                className="relative mx-auto w-full overflow-hidden"
                style={{ height: geom.arcH }}
              >
                {/* DRAG LAYER */}
                <motion.div
                  aria-hidden
                  className="absolute inset-0 touch-pan-y"
                  drag={reduce ? false : 'x'}
                  // small constraint to keep drag alive (no “freeze”), but visually we keep it invisible
                  dragConstraints={{ left: -320, right: 320 }}
                  dragElastic={0.06}
                  dragMomentum={false}
                  onDragStart={reduce ? undefined : onDragStart}
                  onDrag={(e, info) => {
                    if (reduce) return;
                    rot.set(
                      dragStartRotRef.current + info.offset.x * DEG_PER_PX
                    );
                    pauseAuto(11000);
                  }}
                  onDragEnd={reduce ? undefined : onDragEnd}
                />

                {/* STATIC ARC OUTLINE (circle arc feel) */}

                {/* ROTATING RING: ticks + side markers */}
                <motion.div
                  className="absolute"
                  style={
                    {
                      left: '50%',
                      top: geom.peakY,
                      width: geom.size,
                      height: geom.size,

                      // ✅ ez marad meg Framer mellett is (nem “eszi meg” a transformot)
                      x: -geom.size / 2,

                      rotate: rot,

                      // used by ticks to counter-rotate to vertical
                      ['--dial' as any]: dialCss,
                    } as any
                  }
                >
                  {/* ticks (vertical, scale-like, major/mid/minor) */}
                  {ticks.map((i) => {
                    const t =
                      geom.tickCount <= 1 ? 0.5 : i / (geom.tickCount - 1);
                    const a = geom.tickStart + t * geom.tickSpan;

                    const majorEvery = geom.isMobile ? 13 : 16;
                    const midEvery = geom.isMobile ? 6 : 8;

                    const major = i % majorEvery === 0;
                    const mid = i % midEvery === 0;

                    // longer ticks like reference
                    const L = major
                      ? geom.isMobile
                        ? 48
                        : 60
                      : mid
                        ? geom.isMobile
                          ? 34
                          : 42
                        : geom.isMobile
                          ? 20
                          : 24;

                    const op = major ? 0.3 : mid ? 0.2 : 0.14;

                    return (
                      <Tick
                        key={`tick-${i}`}
                        a={a}
                        radius={geom.radius}
                        len={L}
                        opacity={op}
                      />
                    );
                  })}

                  {/* step markers (hide the one under the center marker => prevents gold double-line) */}
                  {resolvedSteps.map((s, i) => {
                    const hidden = i === displayIndex;

                    // stems are long like reference
                    const stemLen = hidden ? 0 : geom.isMobile ? 108 : 126;

                    return (
                      <StepMarker
                        key={`marker-${i}`}
                        angleDeg={geom.stepAngles[i] ?? 0}
                        radius={geom.radius}
                        stemLen={stemLen}
                        step={s}
                        index={i}
                        rot={rot}
                        reduce={!!reduce}
                        isMobile={geom.isMobile}
                        hidden={hidden}
                        onPick={() => {
                          pauseAuto(11000);
                          setActive(i);
                        }}
                      />
                    );
                  })}
                </motion.div>

                {/* CENTER MARKER (FIXED @ x=50%): single gold line + single diamond + top label */}
                <div
                  aria-hidden
                  className="pointer-events-none absolute inset-0"
                >
                  {/* top label */}
                  <div
                    className="absolute left-1/2 text-xs font-semibold tracking-wide text-white/70"
                    style={{
                      top: marker.labelY,
                      transform: 'translateX(-50%)',
                    }}
                  >
                    {pad2(displayIndex + 1)}
                  </div>

                  {/* diamond */}

                  {/* single gold line */}
                  <div
                    className="absolute left-1/2 w-[2px]"
                    style={{
                      top: marker.lineTop,
                      height: Math.max(0, marker.lineBottom - marker.lineTop),
                      transform: 'translateX(-1px)',
                      background:
                        'linear-gradient(to bottom, rgba(178,147,61,0), rgba(178,147,61,0.95), rgba(178,147,61,0.14))',
                      filter: 'drop-shadow(0 18px 26px rgba(0,0,0,0.35))',
                    }}
                  />

                  {/* center icon + title (fixed under marker) */}
                  <div
                    className="absolute left-1/2"
                    style={{ top: marker.iconY, transform: 'translateX(-50%)' }}
                  >
                    <displayStep.icon
                      aria-hidden
                      className="h-7 w-7 text-white/85"
                    />
                  </div>
                  <div
                    className="absolute left-1/2 px-6 text-center text-lg font-semibold tracking-tight text-white"
                    style={{
                      top: marker.titleY,
                      transform: 'translateX(-50%)',
                    }}
                  >
                    {displayStep.title}
                  </div>
                </div>
              </div>

              {/* description (always open, no “Részletek” rendered) */}
              <div className="mt-8 flex flex-col items-center text-center">
                <div className="mx-auto max-w-2xl text-sm leading-relaxed text-white/58">
                  {displayStep.description}

                  {displayStep.ctaText && displayStep.ctaHref ? (
                    <div className="mt-5">
                      <motion.a
                        href={displayStep.ctaHref}
                        className={[
                          'inline-flex items-center gap-2 rounded-full',
                          'bg-white/10 px-5 py-2 text-sm text-white/85',
                          'shadow-[inset_0_0_0_1px_rgba(255,255,255,0.08),0_16px_34px_rgba(0,0,0,0.35)]',
                          'hover:bg-white/12 hover:text-white',
                          'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-white/25',
                        ].join(' ')}
                        whileHover={reduce ? undefined : { y: -1 }}
                        whileTap={reduce ? undefined : { y: 0, scale: 0.99 }}
                        transition={
                          reduce
                            ? undefined
                            : { type: 'spring', stiffness: 360, damping: 26 }
                        }
                      >
                        {displayStep.ctaText}
                        <span aria-hidden className="text-white/60">
                          →
                        </span>
                      </motion.a>
                    </div>
                  ) : null}
                </div>
              </div>

              {/* Részletek gomb: későbbre, most komment */}
              {/*
              <div className="absolute bottom-8 right-8 md:bottom-10 md:right-10">
                <button
                  type="button"
                  onClick={() => {}}
                  className="text-sm text-white/60 hover:text-white/80 underline-offset-4 hover:underline focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-white/25 rounded"
                >
                  Részletek
                </button>
              </div>
              */}
            </div>

            {/* SR mapping */}
            <div className="sr-only" aria-hidden>
              {resolvedSteps.map((s, i) => (
                <span key={`sr-step-${i}`}>{s.title}</span>
              ))}
            </div>
          </div>
        </div>
      </Container>
    </section>
  );
};
